import { createRouter, createWebHistory } from "vue-router";
import SculptureCam from "../views/SculptureCam.vue";
import AboutView from "../views/AboutView.vue";
import PaintingCam from "../views/PaintingCam.vue";
import Paintings from "../views/Paintings.vue";
import Sculptures from "../views/Sculptures.vue";
import QuizCam from "../views/QuizCam.vue";

const routes = [
  {
    path: "/",
    name: "about",
    component: AboutView,
  },
  {
    path: "/sculptures",
    name: "sculptures",
    component: Sculptures,
  },
  {
    path: "/sculpture-camera",
    name: "SculptureCam",
    component: SculptureCam,
  },
  {
    path: "/paintings",
    name: "paintings",
    component: Paintings,
  },
  {
    path: "/painting-camera",
    name: "PaintingCam",
    component: PaintingCam,
  },
  {
    path: "/quiz-camera",
    name: "QuizCam",
    component: QuizCam,
  },
];

const router = createRouter({
  history: createWebHistory(),
  mode: "hash",
  routes,
});

export default router;
