          aria-labelledby="headingOne"
          data-bs-parent="#accordionExample">
          <div class="accordion-body">
            <strong>by Leonardo Da Vinci</strong>
            <p class="card-text">
              The painting represents the scene of the Last Supper of Jesus with
              the Twelve Apostles, as it is told in the Gospel of John –
              specifically the moment after Jesus announces that one of his
              apostles will betray him.
            </p>
            <br />
            <p>
              Leonardo was inconsistent in his painting schedule and to
              accommodate that, the artwork was painted with materials such as
              tempera on gesso, pitch, and mastic. Due to the materials used,
              deterioration occurred to the point that little of the original
              painting remains after the last restoration in 1999.
            </p>
            <br />
            <p>
              It is now situated on the dining room walls of a convent called
              Santa Maria Delle Grazie, a church situated in Milan, Italy. It
              will always remain there because it is impossible to move due to
              the surface it was painted on and the frail nature of the
              materials. The size is 15 feet by 29 feet in diameter.
            </p>
          </div>
        </div>
      </div>
      <div class="accordion-item">
