            The School of Athens
          </button>
        </h2>
        <div
          id="collapseTwo"
          class="accordion-collapse collapse"
          aria-labelledby="headingTwo"
          data-bs-parent="#accordionExample">
          <div class="accordion-body">
            <strong>by Raphael</strong>
            <p class="card-text">
              The School of Athens painting symbolizes Philosophy, one of the
              disciplines of the Humanities or areas of human knowledge, which
              the main room it is painted in, the Stanza della Segnatura, aims
              to represent. The other three paintings symbolize “Poetry”,
              “Justice”, and “Theology”.
            </p>
            <br />
            <p>
              In The School of Athens, we see a variety of ancient philosophers
              and scholars debating, contemplating, and calculating. The two
              central figures include the important ancient philosophers Plato
              and Aristotle. In The School of Athens Plato’s index finger of his
              right hand (our left) points to the sky. This is indicative of his
              philosophical beliefs, which focus on more abstract ideas and that
              reality exists in the “realm” of the spiritual world. This is
              compared to Aristotle’s gesture beside him, which gestures to the
              earth below, indicative of his beliefs that reality is in the
              physical form. Plato’s hand gesture could be referring to his
              philosophy called the “Theory of Form”.
            </p>
            <br />
            <p>
              The painting is housed in the Apostolic Palace in the Vatican City
              in Rome, Italy. It is painted in one of the four “Raphael Rooms”,
              namely, the Stanza della Segnatura (“Room of the Segnatura”), what
              used to be the Pope’s study with a library. The method used is the
              fresco method, in which the practice of painting with a mix of
              water and pigment onto freshly laid wall plaster.
            </p>
          </div>
        </div>
      </div>
      <div class="accordion-item">
