          data-bs-parent="#accordionExample">
          <div class="accordion-body">
            <strong>by Michelangelo</strong>
            <p class="card-text">
              At the heart of the Sistine Chapel’s ceiling work is the the
              depiction of God who extends his finger toward the hand a newly
              minted man, Adam, about to imbue him with life (Eve watches from
              the crook of God’s arm). Few artists had ever depicted the scene
              as dramatically as Michelangelo, and even in our secular age, it
              has made it virtually metonymic for the entire story of Genesis in
              contemporary culture. It is so often invoked, cited, or riffed on
              for its religious meaning that it may actually be possible to
              forget just what a terrific act of artistic theater it is.
            </p>
            <br />
            <p>
              The creation of Adam has several figures in it. The Main figures
              being God and Adam.Art historians speculate that Eve has also been
              featured in this painting. The figure by God’s left hand is
              believed to be Eve. That is the only feminine figure in the
              fresco. Adam is also portrayed to have an extra rib. This is
              believed to represent Eve too. Adam, located on the left side, is
              painted in a somewhat relaxed fashion. His figure appears to be
              responding to the imminent touch of God – this is where the title
              comes in. Adam is receiving life directly from the source, and
              through the life given to him – he will, in turn, give life to all
              of humanity. Religious themes provided some of the most
              inspirations content for artists from the Renaissance.
            </p>
            <br />
            <p>
              The painting is part of the Sistine Chapel's ceiling, you have to
              look up to get a good look of the painting.
            </p>
