              Not technically a portrait, but 'tronie', a Dutch word for a
              drawing of a fictional character with exaggerated facial features.
              The girl wears a blue and gold turban and oversized pearl earrings
              is in focus while having only the dark background behind her. The
              Mauritshuis was renovated in 2012-2014. The painting toured the
              United States, Italy, and Japan. Captivating large crowds, it has
              established her as one of the most famous works of art in the
              world.
            </p>
