          data-bs-parent="#accordionExample">
          <div class="accordion-body">
            <strong>by Edvard Munch </strong>
            <p class="card-text">
              It is not a single work of art. According to the British Museum
              blog, there are two paintings, two pastels and then an unspecified
              number of prints after that. A daring theft of two pictorial
              versions of Scream in 1994 and his 2004 helped raise public
              awareness of the work of art. The androgynous figures in the
              foregrounds of Art Nouveau-style paintings seek to suppress a
              piercing shriek coming of nature rather than creating screams. It
              was inspired by Munch's first-hand experience while taking a
              twilight walk in Oslo when the dramatic shades of red overwhelmed
              his senses.
            </p>
